* {
    font-family: monospace;
}

.startPageContent {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    height: 100vh;
    padding-top: 200px;
    box-sizing: border-box;
}

.Compare {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 100%;
    height: 500px;
    position: relative;
    bottom: 50px;
    background-color: #ebedf6;
}

#LogoImg {
    width: 600px;
}

#SubText {
    font-size: 30px;
    color: #4e4e4e;
    position: relative;
    bottom: 85px;
}

#CompareButtonContainer {
    display: flex;
    justify-content: center;
    position: relative;
    bottom: 60px;
}

#CButton1 {
    width: 120px;
    height: 50px;
    border: 2px solid rgb(98, 98, 98);
    border-radius: 15px;
    background-color: #e7e7e7;
    color: rgb(98, 98, 98);
    font-size: 16px;
    margin: 0 10px;
}

#CButton2 {
    width: 150px;
    height: 50px;
    border: 2px solid #9a9a9a;
    border-radius: 15px;
    background-color: rgb(98, 98, 98);
    color: #e7e7e7;
    font-size: 16px;
    margin: 0 10px;
}

#CButton1:hover,
#CButton2:hover {
    opacity: 0.8;
    cursor: pointer;
}
